<template>
    <div>
      <!--  -->
      <div class="header">
        <headerTab title="  "></headerTab>
      </div>        
        <v-card class="pa-5" tile>
            <!-- <h1> </h1> -->
            
            <v-checkbox 
                label="  ?" 
                v-model="WatOpt"
                class="mt-2"
            />
            <v-text-field
                :disabled="Usli_self == false "
                type="number"
                outlined
                required
                clearable
                label="   ()"
                :rules="rule"
                hide-details="auto"
                v-model.number="Usli_shd"
                class="mt-2"
            />
            <v-checkbox 
                info
                hide-details
                label=" ?" 
                v-model="Usli_self"
                class="mt-5"
            />

            <v-text-field
                type="number"
                outlined
                required
                clearable
                :label="WatOpt===true ? heatlabel1: heatlabel2"
                :rules="rule"
                hide-details="auto"
                v-model.number="Heat"
                class="mt-5"
            />
            <v-text-field
                type="number"
                outlined
                required
                clearable
                label="   "
                :rules="rule"
                hide-details="auto"
                v-model.number="K_sred"
                class="mt-5"
            />
            <v-checkbox 
                label="  ?" 
                v-on:change="komutat_Opt"
                class="mt-5"
            />

            <div v-if="komutat_opt">
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    label="  "
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="count_yadr"
                    class="mt-5"
                />
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    :label="WatOpt===true ? komutat_yadrlabel1: komutat_yadrlabel2"
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="komutat_yadr"
                    class="mt-5"
                />
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    label="  "
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="count_dost"
                    class="mt-5"
                />
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    :label="WatOpt===true ? komutat_dostlabel1: komutat_dostlabel2"
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="komutat_dost"
                    class="mt-5"
                />
            </div>

            <v-text-field
                type="number"
                outlined
                required
                clearable
                :label="condition_label1"
                :rules="rule"
                hide-details="auto"
                v-model.number="condition"
                class="mt-5"
            />

            <v-btn 
                @click="start" 
                color="primary"
                class="mt-5"
            ></v-btn>

        </v-card>

        <!--    -->
        <v-snackbar
            v-model="snackbar"
            :timeout="timeout"
            
        >
        {{ errorText }}

            <template v-slot:action="{ attrs }">
                <v-btn
                color="accent"
                text
                v-bind="attrs"
                @click="snackbar = false"
                >
                
                </v-btn>
            </template>
        </v-snackbar>
    <!--   -->
    <v-card
        class="pa-5 mt-5"
        v-show="started"
    >
        <h2>  : </h2>
        <p><strong>{{ final_result }} .</strong></p>
        <v-btn
          @click="started=false"
          class="mt-2"
        ></v-btn>
    </v-card>

    



    </div>
</template>

<script>
import headerTab from './ui/commonUi/header.vue';
export default {
    name: 'my-cooling',
    components: {
        headerTab,
    },
    data:() =>({
        started: false,         //    (  )

        final_result:0,         //  

        komutat_opt: false,     //    
        Usli_self: false,       //       
        Usli_shd: '...',        //   


        snackbar: false,      //   
        timeout: 2500,
        errorText: '     ',


        K_sred: '...',          //     (0-100)

        WatOpt: true,           //     
        //            
        heatlabel2:'   1  (BTU/hr)',
        heatlabel1:'   1  (B)',
        Heat: '...',            //   

        komutat_yadrlabel2:'   (BTU/hr)',
        komutat_yadrlabel1:'   (B)',
        komutat_yadr:'...',     //   
        count_yadr:'...',       //   

        komutat_dostlabel2:'   (BTU/hr)',
        komutat_dostlabel1:'   (B)',
        komutat_dost:'...',     //   
        count_dost:'...',       //   

        condition_label2:'  (BTU/hr)',
        condition_label1:'  ()',
        condition:'...',        //  


        rule: [
            value => !!value || '   .',
            ],                  //   
    }),
    methods:{
        start(){
            this.started = true
            let result = 0;
            if(this.komutat_opt==false){
                this.komutat_yadr = 0
                this.komutat_dost = 0
                this.count_yadr=0
                this.count_dost=0
                this.komutat_klast=0
            }

            console.log(this.WatOpt)
            if(this.WatOpt==false){

                result = (
                    (this.Heat*this.K_sred/100*this.Usli_shd)/3.41 
                    // +this.komutat_klast/3.41
                    +(this.count_yadr*this.komutat_yadr+this.count_dost*this.komutat_dost)/3.41
                    )
                // console.log('result', result)
                // console.log('Heat', this.Heat)
                // console.log('K_sred', this.K_sred)
                // console.log('Usli_shd', this.Usli_shd)
                // console.log('komutat_dost', this.komutat_dost)
                // console.log('komutat_opt', this.komutat_opt)
                // console.log('Condition', this.condition)
                this.final_result= Math.ceil(result/this.condition)
            }else{


                
                result = (this.Heat * (this.K_sred/100) * this.Usli_shd)

                // result += this.komutat_klast

                result += this.count_yadr * this.komutat_yadr + this.count_dost * this.komutat_dost


                // console.log('result', result)
                // console.log('Heat', this.Heat)
                // console.log('K_sred', this.K_sred)
                // console.log('Usli_shd', this.Usli_shd)
                // console.log('komutat_dost', this.komutat_dost)
                // console.log('komutat_opt', this.komutat_opt)
                // console.log('Condition', this.condition)
                this.final_result= Math.ceil(result/this.condition)
            }
            // console.log('AAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAA', this.final_result)
            if (!this.final_result || this.final_result== Infinity){
                this.snackbar = true
                this.started = false
                return
            }
        },
        getusli(){
            this.Usli_shd = localStorage.getItem('usli')
            console.log(this.Usli_shd)
            return this.Usli_shd
        },
        komutat_Opt(){
            
            if(this.komutat_opt==false){
                this.komutat_opt = true
            }else{ this.komutat_opt = false}

            this.komutat_opt != this.komutat_opt
            if(this.komutat_opt==false){
                this.komutat_yadr = 0
                this.komutat_dost = 0
                this.count_yadr=0
                this.count_dost=0
            }else{
                this.komutat_yadr = '...'
                this.komutat_dost = '...'
                this.count_yadr='...'
                this.count_dost='...'
            } 
        },
    },
    mounted(){
        this.getusli()
    }
}
</script>

<style>

</style>